import React from 'react';

const OfferingCard = ({ offeringKey, title, text, openModal, modalContents }) => (
    <div className="offering-card">
        <h3>{title}</h3>
        <p className="offering-text">{text}</p>
        <button className="read-more-btn" onClick={() => openModal('offeringDetails', modalContents[offeringKey])}>
             
        </button>
    </div>
);

const Offerings = ({ openModal, modalContents }) => (
    <div className="page" id="page4">
        <h2 className="content-title"></h2>
        <div className="offerings-grid">
            <OfferingCard
                offeringKey="kedavilakku"
                title=""
                text="            ..."
                openModal={openModal}
                modalContents={modalContents}
            />
            <OfferingCard
                offeringKey="chovvavilakku"
                title="  "
                text="        .      ..."
                openModal={openModal}
                modalContents={modalContents}
            />
            <OfferingCard
                offeringKey="commonofferings"
                title=" "
                text="    .    , ,    ..."
                openModal={openModal}
                modalContents={modalContents}
            />
        </div>
    </div>
);

export default Offerings;