import React, { useEffect, useRef } from 'react';
import theyyam1 from '../assets/kavuotherimg/theyyam1.webp';
import theyyam2 from '../assets/kavuotherimg/theyyam2.webp';
import theyyam3 from '../assets/kavuotherimg/theyyam3.webp';
import theyyam4 from '../assets/kavuotherimg/theyyam4.webp';

/**
 * A custom React hook that uses the Intersection Observer API
 * to detect when an element enters the viewport. It applies a
 * 'is-visible' class to trigger animations.
 *
 * @param {object} options - Configuration options for the IntersectionObserver.
 * @returns {React.RefObject} A ref object to be attached to the container element.
 */
const useIntersectionObserver = (options) => {
    const containerRef = useRef(null);

    useEffect(() => {
        // Create the observer with a callback function
        const observer = new IntersectionObserver((entries) => {
            entries.forEach(entry => {
                // If the element is intersecting (i.e., visible)
                if (entry.isIntersecting) {
                    // Add the class to trigger the CSS animation
                    entry.target.classList.add('is-visible');
                    // Stop observing the element so the animation only runs once
                    observer.unobserve(entry.target);
                }
            });
        }, options);

        const currentRef = containerRef.current;
        if (currentRef) {
            // Find all elements with the '.image-block' class within the container
            const elements = currentRef.querySelectorAll('.image-block');
            // Start observing each of them
            elements.forEach(el => observer.observe(el));
        }

        // Cleanup function: unobserve all elements when the component unmounts
        return () => {
            if (currentRef) {
                const elements = currentRef.querySelectorAll('.image-block');
                elements.forEach(el => observer.unobserve(el));
            }
        };
    }, [options]); // Re-run the effect if the options change

    return containerRef;
};


/**
 * The Theyyam component displays information about the various Theyyams
 * performed at the temple, using an animated grid layout.
 *
 * @param {object} props - The component's properties.
 * @param {function} props.openImageViewer - A function passed from the parent to open the fullscreen image viewer.
 */
const Theyyam = ({ openImageViewer }) => {
    // Get the ref from the custom hook to attach to the grid container
    const theyyamGridRef = useIntersectionObserver({ threshold: 0.2 });

    /**
     * Handles clicks on the image grid. It uses event delegation to find
     * the clicked image's source and calls the openImageViewer function.
     * @param {React.MouseEvent} e - The mouse event.
     */
    const handleImageClick = (e) => {
        // Find the closest parent element with the 'clickable-image' class
        const clickableItem = e.target.closest('.clickable-image');
        // If found, get the 'src' from the 'img' tag inside it
        const imgSrc = clickableItem?.querySelector('img')?.src;
        
        // If a valid image source is found, open the viewer
        if (imgSrc) {
            openImageViewer(imgSrc);
        }
    };

    return (
        // The main container for the "Theyyam" page section
        <div className="page" id="page-theyyam">
            <h2 className="content-title"></h2>
            
            {/* The grid container. The ref is attached here, and the click handler uses event delegation. */}
            <div className="theyyam-grid" ref={theyyamGridRef} onClick={handleImageClick}>
                
                {/* Grid Item 1: Image */}
                <div className="theyyam-item image-block clickable-image grid-row-span-2">
                    <img src={theyyam1} alt=" " className="theyyam-image" />
                </div>
                
                {/* Grid Item 2: Text */}
                <div className="theyyam-item text-block grid-col-span-3">
                    <h3></h3>
                    <p>          ,    .   . .      ..   .                                         :                .</p>
                </div>
                
                {/* Grid Item 3: Image */}
                <div className="theyyam-item image-block clickable-image grid-col-span-2">
                    <img src={theyyam2} alt="  " className="theyyam-image" />
                </div>
                
                {/* Grid Item 4: Image */}
                <div className="theyyam-item image-block clickable-image">
                    <img src={theyyam3} alt=" " className="theyyam-image" />
                </div>
                
                {/* Grid Item 5: Text */}
                <div className="theyyam-item text-block grid-col-span-4">
                    <h3>  </h3>
                    <p>                ()    . .   ,      .     . , 500         .              .                                                             .</p>
                </div>
                
                {/* Grid Item 6: Text */}
                <div className="theyyam-item text-block grid-col-span-2">
                    <h3>  </h3>
                    <p>     .          .</p>
                </div>
                
                {/* Grid Item 7: Image */}
                <div className="theyyam-item image-block clickable-image grid-col-span-2">
                    <img src={theyyam4} alt="" className="theyyam-image" />
                </div>
            </div>
        </div>
    );
};

export default Theyyam;
